import React, { useEffect, useState } from 'react';
import TextScramble from '@twistezo/react-text-scramble/lib/TextScramble';
import './Header.css'

type HeaderProp = {
  startAnimate:boolean;
}

const Header = ({startAnimate}:HeaderProp) => {
  const [paused, setPaused] = useState<boolean>(false)
  useEffect(() => {
    const timeout = setTimeout(() => {
      setPaused(true)
    }, 3000)
    return () => clearTimeout(timeout)
  }, [])

  const scrambleTexts = ["loading...", "evolving...", "growing..."]
  const scrambleText1 = ["And we are just getting started."]
  return (
    <header className='radial_container'>
      <div className="radial_circle"></div>
      <div className={`header_text_box ${startAnimate?"show-page":"hide-page"}`}>
        <div>
          <h2 id="scramble_large_text" style={{ display: "flex" }}>
            Noctem is&nbsp;
            <span id="dfsd">
              <TextScramble
                texts={scrambleTexts}
                letterSpeed={50}
                nextLetterSpeed={50}
                pauseTime={400}
                paused={paused}
              />
            </span>&nbsp;
          </h2>
          <h2 id="scramble_small_text">
            <span>
              {paused &&
                <TextScramble
                  texts={scrambleText1}
                  letterSpeed={25}
                  nextLetterSpeed={50}
                  pauseTime={500}
                />
              }
            </span>
          </h2>
        </div>
      </div>
      <div></div>

    </header>
  )
}

export default Header