import React from 'react'
import TextScramble from '@twistezo/react-text-scramble/lib/TextScramble';
import handleViewport from 'react-in-viewport';

function List({ inViewport, forwardedRef }) {
    console.log(inViewport)
    return (
        <div className="project_evolv_left helvetica" ref={forwardedRef}>
            <div>
                <h3>Project Evolv</h3>
                <p>Our C# essential tool kit</p>
                <p style={{ fontWeight: "700", fontSize: "32px" }}>Features:</p>
                <ul className="toolkitlist">
                    <li style={{display:"flex"}}>{inViewport && <TextScramble texts={["Navigation"]} letterSpeed={50} nextLetterSpeed={100} pauseTime={500} />}</li>
                    <li style={{display:"flex"}}>{inViewport && <TextScramble texts={["Dialog"]} letterSpeed={50} nextLetterSpeed={100} pauseTime={500} />}</li>
                    <li style={{display:"flex"}}>{inViewport && <TextScramble texts={["Themes, Icon, Controls"]} letterSpeed={50} nextLetterSpeed={100} pauseTime={500} />}</li>
                    <li style={{display:"flex"}}>{inViewport && <TextScramble texts={["Search"]} letterSpeed={50} nextLetterSpeed={100} pauseTime={500} />}</li>
                    <li style={{display:"flex"}}>{inViewport && <TextScramble texts={["Settings"]} letterSpeed={50} nextLetterSpeed={100} pauseTime={500} />}</li>
                    <li style={{display:"flex"}}>{inViewport && <TextScramble texts={["Help"]} letterSpeed={50} nextLetterSpeed={100} pauseTime={500} />}</li>
                </ul>
            </div>
        </div>
    )
}

const ScramblingList = handleViewport(List);


export default ScramblingList