import React from 'react';
import { useState } from 'react';
import CookiePopup from './CookiePopup';
import './Footer.css'

type FooterProp = {
    startAnimate:boolean;
}

const Footer = ({startAnimate}:FooterProp) => {
  const [open,setOpen] = useState<boolean>(true)
    return (
        <footer className={`${startAnimate?"":"hide-main-section"}`}>
            <div className="radial_circle6"></div>
            <div className="radial_circle7"></div>
            <div>
                <img src={require("../img/footer.png")} alt="logo" width="150px" />
                <div className="contact_text">
                    <h6>Contact Us:</h6>
                    <p>(800)990-2705</p>
                    <p>info@NCTMCO.com</p>
                </div>
            </div>
            <p className='footer_para'>Noctem Computing, LLC 2019. All rights reserved. <span>Terms of Use l Privacy Policy</span></p>
            <hr />
            <div className='cookie_popup'>
                <p>Why we use cookies <span><i className="fa-solid fa-chevron-down" onClick={()=>{
                    setOpen(true)
                }}></i></span> </p>
            </div>
            <CookiePopup setOpen={setOpen}/>
        </footer>
    )
}

export default Footer