import React, { useEffect, useState } from 'react'
import Footer from './Footer'
import Header from './Header'
import Main from './Main'

const Home = () => {
  const [startAnimate, setStartAnimate] = useState<boolean>(false)
  // const vidRef = useRef();
  // useEffect(() => { vidRef.current.play(); }, []);
  useEffect(() => {
    setTimeout(() => {
      setStartAnimate(true)
    }, 4500)
  }, [])
  return (
    <>
      <nav>
        <img src={require("../img/logo.png")} alt="logo" width='100px' />
        <div className="links wtcolor">
          <a href=".#" id="servicelang">Services</a>
          <a href=".#" id="employmentlang">Employment</a>
          <a href=".#" id="servicelang">Investor</a>
          <a href=".#" id="contactuslang"><select id="langselector">
            <option id='languagelang' style={{ display: 'none' }}>Language</option>
            <option value="en" id='en' className='translate'>English</option>
            <option value="sp" id='sp' className='translate'>Spanish</option>
            <option value="md" id='md' className='translate'>Mandarin</option>
          </select>
          </a>
          <a href=".#" id="contactuslang">Contact Us</a>
        </div>
      </nav>
      <Header startAnimate={startAnimate}/>
      <Main startAnimate={startAnimate}/>
      <Footer startAnimate={startAnimate}/>
    </>
  )
}

export default Home